// navbar
.navbar {
    // position: fixed
    // top: 0
    // left: 0
    margin: 0;
    background: rgba(42, 5, 66, 0.75);//rgba(29,10,41,0.75)
    border: none;
    border-radius: 0;

    &.navbar-default {
        // border-bottom: 1px solid #7FFFD4//#FFADE1

        .navbar-nav > li > a:hover,
        .navbar-nav > li > a:focus {
            color: #FFA0DC; } }

    #navbar {
        border: none; }

    .nav {
        text-align: center;
        li {
            a {
                padding-top: 20px;
                padding-bottom: 20px; } } }

    .navbar-nav {
        > li {
            a {
                color: white;
                font-size: 14px;
                letter-spacing: 1.5px;
                font-weight: 100;
                text-transform: uppercase; }
            a:hover {
                color: #FFA0DC; }

            &:focus {
                color: #FFA0DC; }

            &.active {
                a {
                    color: #7FFFD4;
                    background: none;
                    &:hover {
                        background: none;
                        color: #7FFFD4; } } } } } }

.navbar .navbar-nav > li.active {
    background: none; }

// hamburger menu
.navbar-default .navbar-header .navbar-toggle {
    border-color: #FFA0DC;
    background: #FFA0DC;
    transition: all 0.1s ease-in-out;
    .icon-bar {
        background: #1D0A29; }

    &.collapsed {
        background: none;
        .icon-bar {
            background: #FFA0DC; } } }

@media only screen and (min-width : 768px) {
    .navbar.navbar-animated {
        background: rgba(29,10,41,0) !important;
        transition: padding 0.5s ease-in-out, background 0.5s ease-in-out, border-color 0.5s ease-in-out;
        padding: 25px 0;
 }        // border-bottom: 1px solid rgba(127, 255, 212,0) !important

    .navbar.navbar-animated.compact-navbar {
        background: rgba(29,10,41,0.75) !important;
        padding: 0 !important;
 } }        // border-bottom: 1px solid rgba(127, 255, 212, 1) !important//rgba(255,173,225,1) !important

@media only screen and (min-width : 768px) and (max-width : 920px) {
    #navbar .navbar-nav > li > a {
        font-size: 14px;
        letter-spacing: 1.5px; } }

// upload button
.btn-file {
    position: relative;
    overflow: hidden; }

.btn-file input[type=file] {
    position: absolute;
    top: 0;
    right: 0;
    min-width: 100%;
    min-height: 100%;
    font-size: 100px;
    text-align: right;
    filter: alpha(opacity=0);
    opacity: 0;
    outline: none;
    background: white;
    cursor: inherit;
    display: block; }


